import 'isomorphic-fetch';

import {
    BIZ_REQUEST,
    BIZ_RECEIVE,
} from '../constants/biz';

export const bizRequest = n => ({
    type: BIZ_REQUEST,
    amount: n
});

export const bizReceive = (n, stories) => ({
    type: BIZ_RECEIVE,
    amount: n,
    data: stories
});

export const bizPost = n => (dispatch) => {
    dispatch(bizRequest(n));
    return fetch('http://0.0.0.0:9527/api/biz')
    .then((response) => {
        if (response.status >= 400) {
            console.log('net work fail');
        }

        return response.json();
    })
    .then((json) => {
        dispatch(bizReceive(n, json.data));
    }).catch(ex =>
        console.log('parsing failed', ex)
    );
};



